@import url(https://fonts.googleapis.com/css2?family=Fira+Sans:wght@500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
* {
  margin: 0;
  padding: 0;
  font-family: "Roboto", sans-serif;
  --primary: #282838;
  --secondary: #ff6a82;
}

span {
  color: var(--secondary);
  margin: 10px, 0px;
  font-family: "Fira Sans";
}
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html,
body {
  height: 100%;
  width: 100%;
}

.home {
  background-color: var(--primary);
  height: 100%;
}

.home__body{
    height: 100%;
}
.header {
  background-color: #12131e;
  color: white;
  display: flex;
  justify-content: space-between;
  height: 70px;
}

.header__left {
  margin-top: auto;
  margin-bottom: auto;
}
.header__logo > * {
  font-weight: 400;
  margin-left: 20px;
}

.header__right {
  margin-top: auto;
  margin-bottom: auto;
  margin-right: 20px;
}

.MuiSvgIcon-root{
    color: white !important;
}

.gamecards {
  background-color: var(--primary);
  display: flex;
  justify-content: space-evenly;
  overflow: hidden;
  flex-wrap: wrap;
}

.card {
  transition: all 0.2s ease-in-out;
  display: flex;
  flex-direction: column;
  width: 250px;
  height: 400px;
  padding: 50px;
}

.card:hover {
  transform: scale(1.1);
}

.card__body {
  box-shadow: 1px 1px 10px black;
  height: 100%;
  width: 100%;
}

.card__title > * {
  color: white;
  font-weight: 400;
  text-align: center;
  font-size: x-large;
}

.card__image{
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.advertbuild {
  background-color: var(--secondary);
  border-radius: 7px;
  height: 50vh;
  padding: 10px;
  margin: auto;
  width: 90%;
  display: flex;
}
.advertbuild__title span {
  color: white;
  font-weight: 300;
}

.advertbuild__title h1 {
  font-weight: 900;
}

.advertbuild__title {
  flex: 0.25 1;
  margin: auto;
  text-align: center;
}

.advertbuild__summary {
  display: flex;
}

.buildsummary {
  display: flex;
  height: 90%;
  width: 100%;
  justify-content: space-evenly;
  background-color: #fff;
  border-radius: 10px;
  margin: 20px;
}

.buildsummary__loading > * {
  margin: auto;
}

.buildsummary__image {
  display: flex;
  justify-content: space-between;
  height: 300px;
  width: 400px;
  margin: auto;
}

.buildsummary__case {
  object-fit: contain;
  height: 50%;
  margin: auto;
}

.buildsummary__partsImg {
  display: flex;
  flex-direction: column;
  margin: auto;
  height: 90%;
}

.buildsummary__partsImg > img {
  height: 50%;
  object-fit: contain;
  padding: 10px;
}

.buildsummary__text {
  padding: 40px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  background-color: orange;
  border-radius: 5px;
  border-bottom-right-radius: 40px;
}

.buildsummary__text > h1 {
  color: var(--primary);
}

.build {
  background-color: #282838;
}
.build__body {
  height: 100%;
  width: 100%;
}

.build__cards {
  margin: auto;
  width: 90%;
  height: 70%;
  display: flex;
  justify-content: space-evenly;
  flex-wrap: wrap;
}

.build__featured > h1 {
  font-size: 50px;
  font-family: "Fira Sans", sans-serif;
  font-weight: 400;
  color: white;
  text-align: center;
  padding: 40px;
}

.buildcard {
  background-color: white;
  border-radius: 5px;
  border: 5px solid;
  margin: 40px;
  padding: 20px;
  min-width: 400px;
  min-height: 400px;
}

.buildcard:hover {
  cursor: pointer;
}

.buildcard__image {
  display: flex;
  justify-content: space-between;
  height: 300px;
  width: 400px;
}

.buildcard__case {
  object-fit: contain;
  height: 50%;
  margin: auto;
}

.buildcard__partsImg {
  display: flex;
  flex-direction: column;
  margin: auto;
  height: 90%;
}

.buildcard__partsImg > img {
  height: 50%;
  object-fit: contain;
  padding: 10px;
}

.buildcard__loading {
  display: flex;
}

.buildcard__loading > * {
  margin: auto;
}

.buildelab {
  margin: 20px;
  color: white;
  padding: 40px;
}

.buildelab > h1 {
  font-size: 45px;
}

.buildelab__titles{
  border-bottom: 1px solid white;
  margin-bottom: 30px;
}

.buildelab__titles > * {
  margin-bottom: 30px;
}


.part {
  color: white;
  margin-bottom: 50px;
  min-height: 200px;
}
.part__title {
  display: flex;
  margin: 10px;
  margin-left: 0;
  font-size: x-large;
}
.part__line {
  border: 1px solid blue;
  background-color: blue;
  width: 100%;
  margin: 17px;
}

.part__body {
  display: flex;
  justify-content: space-around;
  height: 100px;
}
.part__body > h3 {
  margin: auto;
  font-weight: 600;
  margin-left: 10%;
  font-size: large;
}

.part__image {
  width: 100px;
  object-fit: contain;
}

.part__details {
  display: flex;
  justify-content: space-between;
  padding: 30px;
  padding-left: 0;
  position: relative;
  left: 0;
}

.part__buyBtn{
  background-color: #3b3b53 !important;
}

.part__loading {
  display: flex;
}
.part__loading > * {
  margin: auto;
  color: aliceblue;
}

